html {
  font-size: 100%;
  box-sizing: border-box;
}

*, *::after, *::before {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: var(--fontFamilyInter);
}

h1,
h2,
h3 {
  margin-top: 0;
}

a,
a:visited,
a:active {
  text-decoration: none;
}

:root {
  --limeGreen: hsl(163, 72%, 41%);
  --brightRed: hsl(356, 69%, 56%);
  --facebook: hsl(208, 92%, 53%);
  --twitter: hsl(203, 89%, 53%);
  --instagramStart: hsl(37, 97%, 70%);
  --instagramEnd: hsl(329, 70%, 58%);
  --youtube: hsl(348, 97%, 39%);
  --toggleBgStart: hsl(210, 78%, 56%);
  --toggleBgEnd: hsl(146, 68%, 55%);
  --toggle: hsl(230, 22%, 74%);
  --darkBg: hsl(230, 17%, 14%);
  --darkTopBg: hsl(232, 19%, 15%);
  --darkCardBg: hsl(228, 28%, 20%);
  --darkDesaturetedBlueText: hsl(228, 34%, 66%);
  --darkWhite: hsl(0, 0%, 100%);
  --lightBg: hsl(0, 0%, 100%);
  --lightTopBg: hsl(225, 100%, 98%);
  --lightCardBg: hsl(227, 47%, 96%);
  --lightGrayishBlueText: hsl(228, 12%, 44%);
  --lightDarkBlue: hsl(230, 17%, 14%);
}

:root {
  --fontFamilyInter: "Inter", sans-serif;
}

.container {
  padding: 0 1.5625rem;
  max-width: 69.375rem;
  margin: 0 auto;
}
@media (min-width: 59.375em) {
  .container {
    padding: 0;
  }
}

.card {
  padding: 1.25rem;
}

.toggle__button {
  width: 1rem;
  height: 1rem;
  border-radius: 50%;
  background-color: var(--toggle);
}